:root {
  --transition-timing: cubic-bezier(.4, 0, .2, 1);
  --bgc-primary: #fefefe;
  --contour-img: #ffe550;
  --contour-img-hover: #bbf330;
  --btn-burger: #7eb101;
  --btn-burger-hover: #ffe550;
  --btn-bgc-first: #bbf330;
  --btn-bgc-first-hover: #b5ff00;
  --btn-bgc-second: #bbf330;
  --btn-bgc-second-hover: #bbf330;
  --btn-bgc-third: #fff;
  --btn-bgc-third-hover: #ffe550;
  --btn-bgc-fourth: #bbf330;
  --btn-bgc-fourth-hover: #b5ff00;
}

.container {
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

.header {
  background-color: var(--bgc-primary);
  padding: 10px 0;
}

.header__thumb {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav {
    margin-left: auto;
    display: flex;
  }
}

.nav__list {
  gap: 28px;
  display: flex;
}

.btn-buy {
  max-height: 32px;
  background-color: var(--btn-bgc-first);
  transition-property: background-color;
  transition-duration: .3s;
  transition-timing-function: var(--transition-timing);
  border: 0;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
  padding: 6px 18px;
  font-size: 13px;
  font-weight: 500;
  line-height: 150%;
  display: none;
}

.btn-buy:hover, .btn-buy:focus {
  background-color: var(--btn-bgc-first-hover);
}

@media screen and (min-width: 768px) {
  .btn-buy {
    display: flex;
  }
}

.header-burger__btn {
  width: 28px;
  height: 28px;
  color: var(--btn-burger);
  transition-property: color;
  transition-duration: .3s;
  transition-timing-function: var(--transition-timing);
  background-color: #fff;
  border: 0;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.header-burger__btn:hover, .header-burger__btn:focus {
  color: var(--btn-burger-hover);
}

@media screen and (min-width: 768px) {
  .header-burger__btn {
    display: none;
  }
}

.menu-container {
  width: 130px;
  z-index: 999;
  background-color: var(--bgc-primary);
  border-radius: 30px 0 30px 30px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 50px;
  right: 20px;
  transform: translateX(200%);
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu__item {
  padding: 16px;
}

.mobile-menu__item:first-child {
  border-top-left-radius: 30px;
}

.mobile-menu__item:last-child {
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.js-close-menu {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border: 0;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.btn__icon {
  fill: currentColor;
}

